import React, { Component } from "react";
import "./App.css";
import $ from "jquery";
import { SectionsContainer, Section, Header } from "react-fullpage";
import Slider from "react-animated-slider";
import "react-animated-slider/build/horizontal.css";
import ReactTooltip from "react-tooltip";
import {MenuItem, ButtonToolbar, DropdownButton} from 'react-bootstrap'

// projects import
var firstproject = require("./assets/firstproject.png");
var secondproject = require("./assets/secondproject.png");
var thirdproject = require("./assets/thirdproject.png");
var fifthproject = require("./assets/fifthproject.png");

// React projects import
var firstReactproject = require("./assets/firstprojectr.png");
var secondReactproject = require("./assets/secondprojectr.png");
var thirdReactproject = require("./assets/thirdprojectr.png");

// D3 projects import
var d1 = require("./assets/d3_1.png");
var d2 = require("./assets/d3_2.png");
var d3 = require("./assets/d3_3.png");
var d4 = require("./assets/d3_4.png");
var d5 = require("./assets/d3_5.png");

// Profile pic, intro and social
var intro = require("./assets/intro.png");
var profilePic = require("./assets/profilePic.png");
var fakePic = require("./assets/fakePic.png");
var mail = require("./assets/mail.png");
var cp = require("./assets/cp.png");
var git = require("./assets/git.png");
var fb = require("./assets/fb.png");

$("#data_projects").click(function() {
  console.log("Hey");
});

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      displayPicture: false,
      mobileHeader: false
    };

    this.changePic = this.changePic.bind(this);
  }

  changePic() {
    this.setState({ displayPicture: !this.state.displayPicture });
  }

  componentDidMount() {
    $("#intro").fadeIn(5000);
  }

  openDropdown = () => {
    this.setState({mobileHeader: !this.state.mobileHeader})
  }

  render() {

    if (this.state.mobileHeader === true) {
      $(".Header-mobile-dropdown").fadeIn(300);
    }
    if (this.state.mobileHeader === false) {
      $(".Header-mobile-dropdown").fadeOut(100);      
    }

    let options = {
      sectionClassName: "section",
      anchors: [
        "home",
        "projects",
        "react_projects",
        "data_projects",
        "contact"
      ],
      scrollBar: false,
      navigation: false,
      verticalAlign: false,
      sectionPaddingTop: "0px",
      sectionPaddingBottom: "0px",
      arrowNavigation: true
    };

    var flipped;
    if (!this.state.displayPicture) {
      flipped = "";
    } else {
      flipped = "flipped";
    }

    const projects = [
      {
        description:
          "A basic Simon memory game. A pattern of colors starts forming when the Start button is pressed, and you have to get the correct sequence to progress to the next level. Simple.",
        image: firstproject,
        url: "https://s.codepen.io/crpdomingues/debug/LdRjPV/nqkwveLxxoaA"
      },
      {
        description:
          "A calculator. There's really nothing to say about this one.",
        image: secondproject,
        url: "https://s.codepen.io/crpdomingues/debug/dmYJWq/yoAZEQdjjOyr"
      },
      {
        description:
          "A typical Pomodoro clock. You set the session and break lengths (in minutes) and press the Start button. A progress bar fills up untill session length is reached and then empties according to the specified break length. ",
        image: thirdproject,
        url: "https://s.codepen.io/crpdomingues/debug/wmKpVz/VGAWNnqKKowr"
      },
      {
        description:
          "Simple tool that displays the five Wikipedia entries that correspond to the search you've entered.",
        image: fifthproject,
        url: "https://s.codepen.io/crpdomingues/debug/RQExyW/GnrnbVDjjVZr"
      }
    ];

    const reactprojects = [
      {
        description:
          "A very simple roguelike dungeon crawler in which you play as a prisioner trying to escape from a four leveled jail.",
        image: firstReactproject,
        url: "https://crpdomingues.github.io/prison-escape/"
      },
      {
        description:
          "A Game of Life, a cellular automaton originally created by John Horton Conway. If you are not familiar with it, follow the instructions provided.",
        image: secondReactproject,
        url: "https://crpdomingues.github.io/game-of-life/"
      },
      {
        description:
          "A markdown previewer. Insert Git-flavoured Markdown on the first box and see how it translates on the second one.",
        image: thirdReactproject,
        url: "https://crpdomingues.github.io/markdown2gfm/"
      }
    ];
    var dropdownMobileCaret = <i className="fa fa-bars"/>;

    return (
      <div className="main">

        <ReactTooltip className="tooltip" place="top" type="dark" effect="float" />

        <Header>
          <div className="Header">
            <a href="#home">
              <i className="fa fa-square" id="home" />
            </a>
            <a href="#projects">randoms</a>
            <a href="#react_projects">react</a>
            <a href="#data_projects">data visualization</a>
            <a href="#contact">contacts</a>
          </div>

          <div className="Header-mobile">  
            <button onClick={this.openDropdown}>{dropdownMobileCaret}</button>
          </div>
          <div className="Header-mobile-dropdown">
            <a href="#home">home</a>
            <a href="#projects">randoms</a>
            <a href="#react_projects">react</a>
            <a href="#data_projects">data visualization</a>
            <a href="#contact">contacts</a>
          </div>
        </Header>

        <SectionsContainer {...options}>
          <Section className="section" verticalAlign="true" color="whitesmoke">
            <div id="intro">
              <img id="introLogo" src={intro} alt="" />
              <div id="introDivider" />
              <h3>d e v &nbsp; w a n n a b e</h3>
            </div>
          </Section>

          <Section className="section" verticalAlign="true" color="whitesmoke">
            <Slider>
              {projects.map((projects, index) => (
                <div key={index}>
                  <a
                    target="_blank"
                    data-tip={projects.description}
                    href={projects.url}
                  >
                    <img alt="" src={projects.image} />
                  </a>
                  <div className="indicator">
                    <div className="indicatorChild">{index + 1} / 4</div>
                  </div>
                </div>
              ))}
            </Slider>
          </Section>

          <Section className="section" color="whitesmoke">
            <Slider>
              {reactprojects.map((reactprojects, index) => (
                <div key={index}>
                  <a
                    target="_blank"
                    data-tip={reactprojects.description}
                    href={reactprojects.url}
                  >
                    <img alt="" src={reactprojects.image} />
                  </a>
                  <div className="indicator">
                    <div className="indicatorChild">{index + 1} / 3</div>
                  </div>
                </div>
              ))}
            </Slider>
          </Section>

          <Section className="section" verticalAlign="true" color="whitesmoke">
            <div className="d3holder">
              <div className="d3div">
                <a
                  target="_blank"
                  href="https://s.codepen.io/crpdomingues/debug/wmbQdN/VGAWNnqgmRbr"
                >
                  <img alt="" src={d1} />
                  <div className="d3label">Scatterplot Graph<div className="d3label_label">d3</div></div>
                </a>
              </div>
              <div className="d3div">
                <a
                  target="_blank"
                  href="https://s.codepen.io/crpdomingues/debug/xWvomE/mVkbGEPxZgbM"
                >
                  <img alt="" src={d2} />
                  <div className="d3label">Mapped data<div className="d3label_label">d3</div></div>
                </a>
              </div>
              <div className="d3div">
                <a
                  target="_blank"
                  href="https://s.codepen.io/crpdomingues/debug/WzBGbw/wQrPoZndLbgM"
                >
                  <img alt="" src={d3} />
                  <div className="d3label">Bar chart<div className="d3label_label">d3</div></div>
                </a>
              </div>
              <div className="d3div">
                <a
                  target="_blank"
                  href="https://s.codepen.io/crpdomingues/debug/EEqLQw/mWAoNbnPVZyr"
                >
                  <img alt="" src={d4} />
                  <div className="d3label">Force directed graph<div className="d3label_label">d3</div></div>
                </a>
              </div>
              <div className="d3div">
                <a
                  target="_blank"
                  href="https://s.codepen.io/crpdomingues/debug/pLXrZw/LDkmdVZGoOaA"
                >
                  <img alt="" src={d5} />
                  <div className="d3label">Heat map<div className="d3label_label">d3</div></div>
                </a>
              </div>
            </div>
          </Section>
          <Section className="section" verticalAlign="true" color="whitesmoke">
            <div className="container">
              <div id="card" className={flipped}>
                <figure className="front">
                  <img
                    id="fakePic"
                    src={fakePic}
                    onClick={this.changePic}
                    alt=""
                  />
                </figure>

                <figure className="back">
                  <img
                    id="profilePic"
                    src={profilePic}
                    onClick={this.changePic}
                    alt=""
                  />
                </figure>
              </div>
            </div>

            <div className="social">
              <a href="https://github.com/crpdomingues" target="_blank">
                <img src={git} alt="" />
              </a>
              <a href="https://codepen.io/crpdomingues/" target="_blank">
                <img src={cp} alt="" />
              </a>
              <a href="http://facebook.com/domzen" target="_blank">
                <img src={fb} alt="" />
              </a>
              <a
                data-tip="crpdomingues@gmail.com"
                href="mailto:crpdomingues@gmail.com"
                target="_blank"
              >
                <img src={mail} alt="" />
              </a>
            </div>
          </Section>
        </SectionsContainer>
      </div>
    );
  }
}

export default App;


